import React from "react";
import { Link } from '../Link/Link';

import { regionObj } from "../types/regionObj";
interface RegionsListProps {
  regions: regionObj[];
}

const RegionsList: React.FC<RegionsListProps> = ({ regions }) => {
  return (<div>

    <section>
      {regions.map((region) => {
        return <div
          key={region.id}>
          <div>
            <Link
              color="dark"
              text={region.name}
              url={`/regions/${region.id}`} />

          </div>

          <div className="champion__Wrapper">
            <div className="champion__card">

              <img className="Champion__img" alt="champ" src={region.img} ></img>

              <div className="champion__title">

              </div>
              <div className="Champion__info">
                <div className="champion__title">
                  <h1 className="Champion__h1">{region.name}</h1>

                </div>

              </div>

            </div>
          </div>


        </div>
      })}
    </section>
  </div>);
}
export default RegionsList;